import React, { useState } from 'react';
import styled, { css } from 'styled-components';
import { MdDone, MdDelete, MdEdit, MdSave, MdCancel } from 'react-icons/md'
import { useTodoDispatch } from '../../context/TodoContext';
import { doneTodoInGoogleSheet, editTodoTextInGoogleSheet, removeTodoFromGoogleSheet } from '../../util/google.sheets';
import { useAuthState } from '../../context/AuthContext';

const RemoveButton = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    color: #dee2e6;
    font-size: 19px;
    cursor: pointer;
    margin-left: 10px;

    &:hover {
        color: #ff6b6b;
    }

    display: none;
`;

const EditButton = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    color: #dee2e6;
    font-size: 19px;
    cursor: pointer;
    margin-left: 10px;

    &:hover {
        color: #ff6b6b;
    }

    display: none;
`;

const SaveButton = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    color: #dee2e6;
    font-size: 19px;
    cursor: pointer;
    margin-left: 10px;

    &:hover {
        color: #ff6b6b;
    }
`;

const CancelButton = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    color: #dee2e6;
    font-size: 19px;
    cursor: pointer;
    margin-left: 10px;

    &:hover {
        color: #ff6b6b;
    }
`;

const TodoItemWapper = styled.div`
    display: flex;
    align-items: center;
    padding-top: 12px;
    padding-bottom: 12px;
    &:hover {
        ${EditButton},
        ${RemoveButton} {
            display: initial;
        }
    }
`

const TodoCheckBox = styled.div`
    width: 20px;
    height: 20px;
    border-radius: 16px;
    border: 1px solid #ced4da;
    font-size: 22px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
    cursor: pointer;
    ${props =>
        props.done &&
        css`
            border: 1px solid #38d9a9;
            color: #38d9a9;
        `
    }
`;

const TodoText = styled.div`
    flex: 1;
    font-size: 20px;
    color: #495057;
    ${props =>
        props.done &&
        css`
            color: #ced4da;
        `
    }
`;

const TodoForm = styled.form`
    flex: 1;
    display: flex;
    justify-content: center;
    font-size: 20px;
`

const TodoInput = styled.input`
    width: 100%;
    color: #495057;
    font-size: 20px;
`;

const TodoItem = ({ group_id, todo_id, text, done }) => {
    const auth = useAuthState();
    const dispatch = useTodoDispatch();

    const [edit, setEdit] = useState(false);
    const [editText, setEditText] = useState(text);

    const onTextChange = (event) => { setEditText(event.target.value) }
    const onEdit = () => setEdit(true);

    const onDone = async () => {
        const uuid = auth.uuid;
        await doneTodoInGoogleSheet(uuid, group_id, todo_id, !done)
            .then(() => dispatch({ type: "TODO_DONE", group_id, todo_id }))
            .catch(error => console.log(error))
    }

    const onRemove = async () => {
        const uuid = auth.uuid;
        await removeTodoFromGoogleSheet(uuid, group_id, todo_id)
            .then(() => dispatch({ type: "TODO_REMOVE", group_id, todo_id }))
            .catch(error => console.log(error));
    }

    const onSave = async (event) => {
        event.preventDefault();

        const uuid = auth.uuid;
        await editTodoTextInGoogleSheet(uuid, group_id, todo_id, editText)
            .then(() => {
                dispatch({ type: "TODO_CHANGE", group_id, todo_id, editText })
                setEdit(false);
            })
            .catch(error => console.log(error));
    }

    const onCancel = () => {
        setEditText(text);
        setEdit(false);
    }

    return (
        <TodoItemWapper>
            <TodoCheckBox done={done} onClick={onDone}>
                {done && <MdDone />}
            </TodoCheckBox>
            {
                edit
                    ? (
                        <TodoForm onSubmit={onSave}>
                            <TodoInput type="text" value={editText} onChange={onTextChange} />
                        </TodoForm>
                    )
                    : <TodoText done={done}>{text}</TodoText>

            }
            {
                edit
                    ? (
                        <>
                            <SaveButton onClick={onSave}>
                                <MdSave />
                            </SaveButton>
                            <CancelButton onClick={onCancel}>
                                <MdCancel />
                            </CancelButton>
                        </>
                    ) : (
                        <>
                            <EditButton onClick={onEdit}>
                                <MdEdit />
                            </EditButton>
                            <RemoveButton onClick={onRemove}>
                                <MdDelete />
                            </RemoveButton>
                        </>
                    )
            }
        </TodoItemWapper>
    )
}

export default TodoItem;