import React from 'react';
import styled from 'styled-components';
import Navbar from './Navbar';

const HeaderBackground = styled.div`
    width: 100%;
    height: 80px;
    background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
    color: #ffffff;
    display: flex;
    justify-content: space-between;
    align-items: center;
`
const HeaderWrapper = styled.div`
    display: flex;
    align-items: center;
    height: 80px;
    margin-left: 65px;
    align-items: center;

    h1 {
        margin-right: 50px;
        font-size: 35px;
        font-weight: 700;
        text-align: left;
    }

    h2 {
        font-size: 20px;
        font-weight: 400;
        text-align: center;
    }
`

const HeaderNavbarWrapper = styled.div`
    float: right;
`

const Header = ({ auth }) => {
    return (
        <HeaderBackground>
            <HeaderWrapper>
                <h1>React Todo App</h1>
            </HeaderWrapper>
            <HeaderNavbarWrapper>
                <Navbar auth={auth} />
            </HeaderNavbarWrapper>
        </HeaderBackground>
    )
}

export default Header;
