import React, { useState, memo } from 'react';
import { MdCancel, MdDelete, MdEdit, MdSave } from 'react-icons/md';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { useAuthState } from '../../context/AuthContext';
import { useTodoDispatch } from '../../context/TodoContext';
import { editGroupTitleInGoogleSheet, removeGroupFromGoogleSheet } from '../../util/google.sheets';

const RemoveButton = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    color: #dee2e6;
    font-size: 19px;
    cursor: pointer;
    margin-left: 10px;

    &:hover {
        color: #ff6b6b;
    }

    display: none;
`;

const EditButton = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    color: #dee2e6;
    font-size: 19px;
    cursor: pointer;
    margin-left: 10px;

    &:hover {
        color: #ff6b6b;
    }

    display: none;
`;

const SaveButton = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    color: #dee2e6;
    font-size: 19px;
    cursor: pointer;
    margin-left: 10px;

    &:hover {
        color: #ff6b6b;
    }
`;

const CancelButton = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    color: #dee2e6;
    font-size: 19px;
    cursor: pointer;
    margin-left: 10px;

    &:hover {x
        color: #ff6b6b;
    }
`;

const GroupItemWapper = styled.div`
    display: flex;
    align-items: center;
    padding-top: 12px;
    padding-bottom: 12px;
    &:hover {
        ${EditButton},
        ${RemoveButton} {
            display: initial;
        }
    }
`
const GroupText = styled.div`
    flex: 1;
    font-size: 20px;
    color: #495057;
`;

const GroupLink = styled(Link)`
    text-decoration: none;
`;

const GroupForm = styled.form`
    flex: 1;
    display: flex;
    justify-content: center;
    font-size: 20px;
`

const GroupInput = styled.input`
    width: 100%;
    font-size: 20px;
    color: #495057;
    margin-left: 5px;
`;

const GroupItem = ({ group_id, title }) => {
    const auth = useAuthState();
    const dispatch = useTodoDispatch();
    const [edit, setEdit] = useState(false);
    const [editTitle, setEditTitle] = useState(title);

    const onTitleChange = (event) => setEditTitle(event.target.value)
    const onEdit = () => setEdit(true);

    const onRemove = async () => {
        const uuid = auth.uuid;
        dispatch({ type: "GROUP_REMOVE", uuid, group_id })
        setEdit(false);
        await removeGroupFromGoogleSheet(uuid, group_id)
            .catch(error => console.log(error));
    }

    const onSave = async (event) => {
        event.preventDefault();
        const uuid = auth.uuid;
        dispatch({ type: "GROUP_CHANGE", group_id, editTitle });
        setEdit(false);
        await editGroupTitleInGoogleSheet(uuid, group_id, editTitle)
            .catch(error => console.log(error));
    }

    const onCancel = () => {
        setEditTitle(title);
        setEdit(false);
    }

    return (
        <GroupItemWapper>
            {
                edit
                    ? (
                        <GroupForm onSubmit={onSave}>
                            🔗 <GroupInput type="text" value={editTitle} onChange={onTitleChange} />
                        </GroupForm>
                    ) : (
                        <GroupText>
                            <GroupLink
                                to={`/todo/?group_id=${group_id}`}
                            >
                                🔗 {title}
                            </GroupLink>
                        </GroupText>
                    )
            }
            {
                edit
                    ? (
                        <>
                            <SaveButton onClick={onSave}>
                                <MdSave />
                            </SaveButton>
                            <CancelButton onClick={onCancel}>
                                <MdCancel />
                            </CancelButton>
                        </>
                    ) : (
                        <>
                            <EditButton onClick={onEdit}>
                                <MdEdit />
                            </EditButton>
                            <RemoveButton onClick={onRemove}>
                                <MdDelete />
                            </RemoveButton>
                        </>
                    )
            }
        </GroupItemWapper>
    )
}

export default memo(GroupItem);
